a {
  text-decoration: none;
  color: #E0ECFF;
}
* {
  box-sizing: border-box;
}
.testnet-faucet {
  display: flex;
  justify-content: center;
  align-items: center;
}
.testnet-faucet .faucet-info {
  max-width: 1200px;
  min-width: 375px;
  margin-top: 100px;
}
.testnet-faucet .faucet-info .faucet-title {
  font-weight: normal;
  font-size: 32px;
  color: #FFFFFF;
  border-bottom: 1px solid #203B60;
  padding-left: 16px;
  padding-bottom: 10px;
}
.testnet-faucet .faucet-info .mint-token {
  display: flex;
  flex-wrap: wrap;
  padding-left: 16px;
}
.testnet-faucet .faucet-info .mint-token .mint-token-box {
  width: 120px;
  margin-right: 45px;
  margin-top: 54px;
  height: 190px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid #203B60;
  background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
}
.testnet-faucet .faucet-info .mint-token .mint-token-box .token-name,
.testnet-faucet .faucet-info .mint-token .mint-token-box .token-button {
  margin-top: 16px;
  font-weight: 300;
}
.testnet-faucet .faucet-info .tab {
  width: 316px;
  display: flex;
  height: 46px;
  margin-top: 41px;
  margin-left: 16px;
}
.testnet-faucet .faucet-info .tab div {
  width: 50%;
  height: 46px;
  line-height: 46px;
  text-align: center;
  font-size: 20px;
  color: #FFFFFF;
  cursor: pointer;
}
.testnet-faucet .faucet-info .tab .check {
  background: #103C60;
  border-radius: 5px;
}
.testnet-faucet .faucet-info .mint-bnb {
  height: 82px;
  background: rgba(85, 119, 253, 0.1);
  border: 1px solid #203B60;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 16px;
  font-weight: 300;
  font-size: 24px;
  margin-top: 40px;
  flex-wrap: wrap;
}
.testnet-faucet .faucet-info .mint-bnb .bnb-button {
  width: 231px;
  height: 82px;
  border-left: 1px solid #203B60;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testnet-faucet .faucet-info .mint-bnb .bnb-button a {
  width: 92px;
  height: 31px;
  line-height: 31px;
  background-color: #3756CD;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
}
@media only screen and (max-width: 1230px) {
  .testnet-faucet {
    padding: 0 24px;
  }
}
@media only screen and (max-width: 725px) {
  .testnet-faucet {
    padding: 0 24px;
  }
  .testnet-faucet .faucet-info .mint-bnb {
    height: 100%;
    align-items: center;
    justify-content: center;
  }
  .testnet-faucet .faucet-info .mint-bnb .bnb-button {
    width: 375px;
    border: 0;
    border-top: 1px solid #203B60;
  }
}
